	        box-shadow: 3px 13px 21px 0 rgba(0, 0, 0, 0.08);
	width: 68%;
	height: 65vh;
	position: relative;
}
.RoleCreate h1 {
	color: #eee;
	font-size: 16px;
	font-style: normal;
	font-stretch: normal;
	line-height: normal;
	letter-spacing: 1.8px;
	font-weight: normal;
	padding: 22px  5px 19px 24px;
	background-image: -webkit-linear-gradient(357deg, #2467e2, #2b3bc4);
	background-image: -o-linear-gradient(357deg, #2467e2, #2b3bc4);
	background-image: linear-gradient(93deg, #2467e2, #2b3bc4);
}
.RoleCreateBtn {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	position: absolute;
    left: 0;
    right: 0;
    bottom: -9px;
}


